import React from "react";

function Header({ history }) {
    function onClick() {
        const goStart = window.confirm(
            "   \n  ?"
        );

        if (goStart) {
            return history.push("/");
        } else return null;
    }

    return (
        <div>
            <div className="Header" onClick={onClick}>
                {/* <div className="Header"> */}
                <div>  </div>
            </div>
        </div>
    );
}

export default Header;
